export default {
	data() {
		return {
			form:{
				mail: '',
				vCode:''
			},
			userDetail:'',
			showBotton: true,
			count: '',
			timer: null,
		};
	},
	onLoad:function(){
		// this.fetchUserDetail()
	},
	methods:{
		getCode(){
			console.log('sfwfwef')
			 const TIME_COUNT = 120;
			 if (!this.timer) {
				 this.count = TIME_COUNT;
				 this.showBotton = false;
				 this.timer = setInterval(() => {
			          if (this.count > 0 && this.count <= TIME_COUNT) {
			              this.count--;
			           } else {
						  this.showBotton = true;
						  clearInterval(this.timer);
						  this.timer = null;
				       }
			    }, 1000)
			 }
		},
	    async sendVcode(){
			if (this.form.mail == ''){
				uni.showToast({
					title: '郵箱不能為空',
					icon: 'none',
					duration: 1000
				});
				return
			}
			this.getCode()
			let data ={
				mail: this.form.mail
			}
	    	const res  = await this.$http.get('/user/verification',{data: data})
	    	uni.showToast({
	    		title: '發送成功',
				duration:  1000
	    	});
	    	
	    },
		
		async submit(){
			if (this.form.mail == '') { 
				uni.showToast({
					title: '郵箱不能為空',
					icon: 'none'
				});
				return
			}
			uni.showLoading({
				title: '提交中',
				mask: true
			});
			const res = await this.$http.get('/user/bind_mail',{data: this.form})
			uni.hideLoading()
			console.log(res)
			uni.showToast({
				title: '綁定成功',
				icon: 'none',
				duration: 1000
			});
			setTimeout(function() {
				uni.navigateBack({
					delta: 1
				});
			}, 1500);
			
			 
		},
		scanCodeToken:function(){
			uni.scanCode({
			    success: function (res) {
			        console.log('条码类型：' + res.scanType);
			        console.log('条码内容：' + res.result);
			    }
			});
		},
		scanCodeNode: function(){
			uni.scanCode({
			    success: function (res) {
			        console.log('条码类型：' + res.scanType);
			        console.log('条码内容：' + res.result);
			    }
			});
		}
	}
}
